      //   showCancel: false,
      //   success (res) {
      //     console.log(res)
      //   },
      //   fail (res) {
      //     console.log(res)
      //   },
      //   compelete (res) {
      //     console.log(res)
      //   }
      // })
    },

    showmyModal (e) {
      this.showIcon = false
    },

    bindviewTap (e) {
      // wx.request({
      //   url: 'test.php', // 仅为示例，并非真实的接口地址
      //   data: {
      //     x: '',
      //     y: ''
      //   },
      //   header: {
      //     'content-type': 'application/json' // 默认值
      //   },
      //   success (res) {
      //     console.log(res.data)
