</script>

<style scoped>
.top-div-container {
  width: 100%;
  height: 200rpx;
  display: flex;
  font-size: 20px;
  line-height: 30px;
  align-items: center;
  background-color: white;
}

.score{
  color: red;
  font-size: 30px;
  margin-left: 15rpx;
}

.news {
  position: absolute;
  font-size: 10px;
  right: 10rpx;
  color: #a0a0a0;
}

.icon {
  width: 20px;
  height: 20px;
  margin: 0 15px;
}

.integral-container {
  height: 100%;
  width: 750rpx;
  background-color: #e6e6e6;
}

.tab-bar {
  position: relative;
  height: 90rpx;
  width: 100%;
  font-size: 15px;
  background-color: #fafafa;
  display: flex;
  align-items: center;
}

.tab-item {
  position: relative;
  display: flex;
  flex: 1;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.selected {
  color: red;
  border-bottom: 2px solid red;
}

.list-container {
  position: relative;
  width: 100%;
  font-size: 15px;
  margin-top: 15rpx 0;
  background: white;
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
}

.list-item {
  height: 60px;
  display: flex;
  align-items: center;
  position: relative;
}

.list-item::after {
  content: '';
  position: absolute;
  top: 0;
  left: 30rpx;
  width: 100%;
  height: 1px;
  background: #e6e6e6;
}

.list-date {
  font-size: 12px;
  color: #666666;
}
.list-item-right {
  position: absolute;
  left: 30%;
}

.list-left {
  position: absolute;
  left:20rpx;
}

.list-right {
  position: absolute;
  right:20rpx;
}

.my-modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 10%;
  top: 10%;
  width: 80%;
  height: 80%;
  background-size: cover;
  background-color: #e6e6e6;
  border-radius: 10px;
}

.cancel-icon {
  position: absolute;
  width: 50rpx;
  height: 50rpx;
  top: 8%;
  right: 7%;
}

.title {
  font-size: 20px;
  line-height: 40px;
}

.content {
  text-align: justify;
  font-size: 15px;
  padding: 10px 30px;
}

.modal-container {
  position: fixed;
  background-color: rgba(0,0,0, .4);
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}

</style>



